













































import Vue from "vue";
import KLoading from "../../components/KLoading.vue";
import KDialog from "../../components/KDialog.vue";
import KDataList from "../../components/KDataList.vue";

import { Display } from "../../configuration/display";

import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

import {
  ISolicitacaoListaView,
  ISolicitacaoListaController
} from "../../contracts/Solicitacao/SolicitacaoListaContract";

import { SolicitacaoListaController } from "../../controllers/Solicitacao/SolicitacaoListaController";

import Component from "vue-class-component";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class SolicitacaoLista extends Vue
  implements ISolicitacaoListaView {
  _controller?: ISolicitacaoListaController;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  dialogDelete = false;
  itemSolicitacaoRemover: any;

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }
  showMsg(msg: string): void {
    this.textError = msg;
    this.dialogMessage = true;
  }
  showItens(list: any): void {
    this.itens = list;
  }
  fecharDialogMessage() {
    this.dialogMessage = false;
  }

  deleteItem(item: any) {
    this.itemSolicitacaoRemover = item;
    this.dialogDelete = true;
    //this._controller!.deleteitem(item.Id);
  }

  deleteItemConfirmado() {
    this.dialogDelete = false;
    this._controller!.deleteitem(this.itemSolicitacaoRemover.Id);
  }

  editarItem(item: any) {
    this.$router.push("/solicitacao/" + item.Id);
  }

  isMobile = false;

  headers = new Headers([
    new Header("Solicitante", "Solicitante", false, false),
    new Header("Centro Custo", "CentroCusto", false, false),
    new Header("Data Solicitante", "DataSolicitacao", false, false),
    new Header("Data Vencimento", "DataVencimento", false, false),
    new Header("Ações", "acoes", true, false)
  ]);

  itens = [];

  addSolicitacao() {
    this.$router.push("/solicitacao");
  }

  created() {
    this.isMobile = Display.isMobile();
    this._controller = new SolicitacaoListaController(this);

    this._controller.carregarItens();
  }
}
